	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Bridge Day","date":"30 May 2003","slug":"/2003-05-30-bridge-day/","tags":"Y2003"},"html":"<p>It was a mistake going to work today, after the Ascension holiday yesterday. I should have taken the day off like most people. The office wasn't just empty, but seeped in a mood of doom and gloom, as next week we start on reduced time... 'Kurzarbeit'. My department will be working at 80% across the board, which we all find fair, and works out nicely to a 4 day work week. Other departments are harder hit, with some people being cut down to 0%. Those people are certainly wondering if they will ever come back, and if the entire deal was just a way for the firm to get rid of them without a formal termination of employment.</p>\n<p>No, I should have taken today off. Then maybe I could have had as much fun as I did yesterday, when I took Christopher to the zoo and saw <a title=\"Noch'n Blogg.: Elefantenbaby bei Hagenbeck\" href=\"http://lumma.de/mt/archives/000244.html\">the baby elephant</a>, or tomorrow, when we all will head to the beach on the Baltic Sea (Mama has a class reuinion, Christopher and I will come home by train). Or on Monday, when I will take my first 'short day' home with Christopher (his day care and the substitute are both on vacation). All of those are much happier prospects than work.</p>"},"next":{"fields":{"title":"Blogging in German","date":"30 May 2003","slug":"/2003-05-30-blogging-in-german/"}},"previous":{"fields":{"title":"Jason's rules for the NYC subway","date":"30 May 2003","slug":"/2003-05-30-jasons-rules-for-the-nyc-subway/"}}},{"node":{"fields":{"title":"Blogging in German","date":"30 May 2003","slug":"/2003-05-30-blogging-in-german/","tags":"Y2003"},"html":"<p>As an experiment I've reactivated my <a title=\"truantville\" href=\"http://tru.antville.org/\">old antville weblog</a>  to do some posting in German. I imagine that my posts there will be a lot different than what I post here, since I won't have to write a paragraph of background information for a 4 word punch line.</p>"},"next":{"fields":{"title":"I met my wife on-line...","date":"29 May 2003","slug":"/2003-05-29-i-met-my-wife-on-line/"}},"previous":{"fields":{"title":"Bridge Day","date":"30 May 2003","slug":"/2003-05-30-bridge-day/"}}},{"node":{"fields":{"title":"I met my wife on-line...","date":"29 May 2003","slug":"/2003-05-29-i-met-my-wife-on-line/","tags":"Y2003"},"html":"<p><a title=\"AccordionGuy: New Girl: Reloaded\" href=\"http://kode-fu.com/shame/#200357992\"><br />\n'I met my wife on-line. She told me she was 14. I told her I was rich.'</a></p>"},"next":{"fields":{"title":"","date":"29 May 2003","slug":"/2003-05-29-goennen-wirs-dem-papa/"}},"previous":{"fields":{"title":"Blogging in German","date":"30 May 2003","slug":"/2003-05-30-blogging-in-german/"}}}],"pathPrefix":"","first":false,"last":false,"index":719,"pageCount":1101}}

